.lc-history{
	&__top{
		ul{
			display: flex;
			flex-wrap: wrap;
			margin-top: 10px;
			padding: 0 30px;
			li{
				font-size: 14px;
				line-height: 135%;
				color: #919393;
				width: 15%;
				padding: 20px;
				text-align: center;
				&:first-child{
					width: 55%;
					text-align: left;
					padding-left: 0px;
				}
				&:last-child{
					padding-right: 0;
				}
			}
		}
	}
	&__block{
		background: #F8FBFB;
		border: 1px solid #CFD5D5;
		box-sizing: border-box;
		border-radius: 4px;
		display: flex;
		flex-wrap: wrap;
		padding: 30px;
		margin-bottom: 36px;
		width: 100%;
		&-info{
			width: 55%;
			padding-right: 40px;
			&__top{
				display: flex;
				margin-bottom: 12px;
				align-items: center;
				justify-content: space-between;
				.num{
					font-weight: 600;
					font-size: 18px;
					line-height: 135%;
					color: #F74B4B;
					text-decoration: none;
				}
				a{
					font-size: 14px;
					line-height: 135%;
					text-decoration-line: underline;
					color: #1D1A1A;
					&:hover{
						text-decoration: none;
					}
				}
			}
			.item{
				font-weight: 500;
				font-size: 14px;
				line-height: 135%;
				color: #4F5050;
				border-top: 1px dotted #B5BEBE;
				margin-top: 10px;
				padding: 10px 0;
				display: block;
				&:nth-child(2){
					border-top: 0;
				}
				&:hover{
					color: #F74B4B;
				}
			}
		}
		&-simple{
			width: 15%;
			text-align: center;
			padding-top: 50px;
			padding-left: 10px;
			padding-right: 10px;
			border-left: 1px solid #E8ECEC;
			.text{
				max-width: calc(100% - 45px);
			}
			.name{
				display: none;
			}
			span{
				display: flex;
				align-items: center;
				justify-content: center;
				font-weight: 600;
				font-size: 14px;
				line-height: 135%;
				color: #4F5050;
				min-height: 30px;
				.sprite-svg{
					fill: #F74B4B;
					margin-right: 15px;
				}
			}
		}
	}
}
.link--circle-ar{
	display: flex;
	align-items: center;
	font-weight: 500;
	font-size: 16px;
	line-height: 135%;
	color: #F74B4B;
	text-transform: uppercase;
	margin-top: 15px;
	.ar{
		background: #FFFFFF;
		box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
		margin-left: 20px;
		width: 42px;
		height: 42px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: .3s;
		.sprite-svg{
			width: 20px;
		}
	}
	&:hover{
		text-decoration: underline;
		.ar{
			margin-left: 10px;
			box-shadow: none;
		}
	}
}